html {
  margin: 0;
  font-family: Arial, sans-serif, Roboto;
}

body {
  margin: 0;
}

.header {
  box-sizing: border-box;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav_item {
  align-items: center;
  list-style-type: none;
  display: flex;
}

.nav_item:not(:first-child) {
  margin-left: 20px;
}

.nav_link {
  text-transform: uppercase;
  color: #000;
  white-space: nowrap;
  justify-content: center;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.nav .nav_link--is-active {
  color: #00acdc;
  position: relative;
}

.nav_link:hover {
  color: #00acdc;
  transition: all .3s;
}

.nav .nav_link--is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav_list {
  align-items: center;
  height: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.dad_card {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  gap: 48px 46px;
  padding: 50px 40px;
  display: grid;
}

.card {
  width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s;
}

.card:hover {
  color: #34568b;
  transform: scale(1.2);
}

.card_image {
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
  display: flex;
}

.description {
  justify-content: center;
  align-items: center;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.product_code {
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.dad_forStars {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.star_review {
  align-items: center;
  display: flex;
}

.reviews {
  white-space: nowrap;
  margin-left: 8px;
  font-size: 10px;
}

.stars_active, .stars {
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars_active {
  background-image: url("star-active.edda4c43.svg");
}

.stars {
  background-image: url("star.822d561e.svg");
}

.price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.text_price {
  color: #616070;
}

.numeric_price {
  font-family: Roboto, sans-serif, Arial;
  font-weight: 700;
}

.button_buy {
  color: #fff;
  background-color: #00acdc;
  border: solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  display: flex;
}

.button_buy:hover {
  color: #00acdc;
  background-color: #fff;
  transition: all .3s;
}

@media (width >= 488px) {
  .dad_card {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .dad_card {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .dad_card {
    --column-count: 4;
  }
}
